import React, { Component } from 'react';
import SkillPresenter from './SkillPresenter';
import HTMLIMG from 'assets/skill/html5.png';
import CSSIMG from 'assets/skill/css3.png';
import JSIMG from 'assets/skill/javascript.png';
import JQIMG from 'assets/skill/jquery.png';
import REACTIMG from 'assets/skill/react.png';
import VUEIMG from 'assets/skill/vue.png';
import NODEIMG from 'assets/skill/node.png';
import MONGOIMG from 'assets/skill/mongodb.png';
import GITIMG from 'assets/skill/git.png';

class SkillContainer extends Component {
    state = {
        data: [
            {
                skill: "HTML5",
                img: HTMLIMG,
                lv: 3,
                txt: " ,  HTML  ."
            },
            {
                skill: "CSS3",
                img: CSSIMG,
                lv: 3,
                txt: " 2,     ."
            },
            {
                skill: "JavaScript",
                img: JSIMG,
                lv: 3,
                txt: " 3,  this  JavaScript  ES6 ."
            },
            {
                skill: "Jquery",
                img: JQIMG,
                lv: 1,
                txt: " ajax,   ."
            },
            {
                skill: "React.js",
                img: REACTIMG,
                lv: 2,
                txt: "Virtual DOM, State Props, , Router      ."
            },
            {
                skill: "Vue.js",
                img: VUEIMG,
                lv: 2,
                txt: "data, methods,  Vue   Vuex     . "
            },
            {
                skill: "Node.js",
                img: NODEIMG,
                lv: 2,
                txt: "Node.js    ,  Express   API   ."
            },
            {
                skill: "MongoDB",
                img: MONGOIMG,
                lv: 2,
                txt: "NoSQL DBMS, Node.js   Mongoose . "
            }, {
                skill: "Git",
                img: GITIMG,
                lv: 2,
                txt: "Git      ."
            },
        ],
        slideCounter: 0
    }

    increaseCounter = () => {
        const { slideCounter, data } = this.state;
        slideCounter === data.length - 1 ?
            this.setState({
                slideCounter: 0,
            }) :
            this.setState({
                slideCounter: slideCounter + 1,
            })
    }

    decreaseCounter = () => {
        const { slideCounter, data } = this.state;
        slideCounter === 0 ?
            this.setState({
                slideCounter: data.length - 1
            }) :
            this.setState({
                slideCounter: slideCounter - 1,
            })
    }

    render() {
        const { data, slideCounter } = this.state;
        return (
            <SkillPresenter
                data={data}
                slideCounter={slideCounter}
                increaseCounter={this.increaseCounter}
                decreaseCounter={this.decreaseCounter}
            />
        )
    }
}

export default SkillContainer;